.reviews__wrapper {
  padding: 40px 80px;
}

.reviews__item {
  padding: 20px;
  &:not(:last-child) {
    margin-bottom: 29px;
  }
}

.reviews .section__head {
  margin-bottom: 24px;
}

.reviews__task {
  display: block;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  color: $blue;
}

.reviews__quote {
  font-size: 18px;
  line-height: 25px;
}

@media (max-width: 1024px) {
  .reviews__wrapper {
    padding: 25px;
  }
}

@media (max-width: 420px) {
  .reviews__wrapper {
    padding: 25px 10px;
    .section__head {
      padding: 0 25px;
    }
  }
  .reviews__item {
    padding: 10px;
  }
}