.profile__breadcrumbs {
  margin-bottom: 20px;
}
.profile__wrapper {
  border-radius: 20px;
}

.profile__wrapper--order {
  margin-bottom: 30px;
}

.profile__wrapper--clear {
  padding: 60px;
  width: 100%;
  max-width: 640px;
  margin: 0 auto;
  margin-bottom: 47px;
}

.profile__heading {
  margin-bottom: 50px;
}

.profile--element {
  margin-bottom: 10px;
}

.profile-about {
  margin-bottom: 30px;
}

.profile__bg {
  display: block;
  width: 100%;
  height: 220px;
  background: radial-gradient(411.41% 1529.04% at 1.55% 241.88%, #0076CB 0%, #98ECEC 92.98%);
  border-radius: 20px 20px 0px 0px;
}

.profile__box {
  padding: 30px 80px;
  &--add {
    padding-top: 60px;
  }
}

.profile__picture-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -80px;
  margin-bottom: 20px;
}

.profile__avatar {
  width: 100px;
  height: 100px;
}

.profile__name {
  margin-bottom: 5px;
  font-size: 32px;
  line-height: 37px;
  font-weight: 500;
}

.profile__place {
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
}

.profile__header {
  margin-bottom: 40px;
}

.profile--settings {
  .profile__name {
    margin-bottom: 40px;
  }
}

.profile__control {
  margin-top: 50px;
  margin-bottom: 77px;
}

@media (max-width: 1024px) {
  .profile__box {
    padding: 25px;
  }
}

@media (max-width: 768px) {
  .profile {
    .main-title {
      margin-bottom: 30px;
    }
  }
  .profile__wrapper {
    .form__element {
      display: flex;
      flex-direction: column;
      .form__item {
        max-width: 400px;
        margin-bottom: 20px;
        margin-right: 0;
      }
      .form__select--big {
        width: 100%;
        max-width: none;
      }
    }
  }
}

@media (max-width: 600px) {
  .profile__wrapper--clear {
    padding: 25px;
  }
}

@media (max-width: 420px) {
  .profile__box {
    padding: 10px;
    padding-bottom: 30px;
  }
  .profile__wrapper .profile__box {
    padding-top: 30px;
  }
}